function AccumulateTime(Time, PeriodeLength, Times = 1) { //Todo... should only be days now..
  var NewTime = new Date(Time.getTime())
  switch(PeriodeLength) {
    case "Weeks":
      NewTime.setDate(NewTime.getDate() + 7)
      break;
    case "Month":
      NewTime.setMonth(NewTime.getMonth() + 1)
      break;
    case "Days":
    default:
      //86.400.000 ms
      do {
        NewTime.setDate(NewTime.getDate() + 1)
        Times--
      } while(Times > 0)
      break;
  }
  return NewTime;
}
//Get Slope Between two points
function GetSlope_Between(MinMax_Time, MinMax_Data){
  var minX = MinMax_Time[0]
  var maxX = MinMax_Time[1]
  var minY = MinMax_Data[0]
  var maxY = MinMax_Data[1]

  //Slope = DeltaY / DeltaX
  var slope = (maxY - minY) / (maxX - minX)
  if (minY > minX) slope = -slope;
  return slope
}

//Get point expected data and virtual simulation from data.
//ExValue = Expected value for AtTime, if slope continues, for spots given (or inbetween).
//ExTime = Expected epoch time, there slope for given spots, will be ForData
export function get_DataSpot(MinMax_Time, MinMax_Data, AtTime, ForData) {
  var ExValue = null
  var ExTime = null

  var slope = GetSlope_Between(MinMax_Time, MinMax_Data)

  if (AtTime) {
    var FindX = AtTime- MinMax_Time[0]
    // ExValue = (FindX * Slope) + MinY               // Setting FindTime, due b then = Min data.. (b is y = 0)
    ExValue = (FindX * slope) + MinMax_Data[0]          // ( X * a ) + b = Expeted data for spot on CalcTime
  }
  if (ForData) {
    ExTime = (ForData - MinMax_Data[0]) / slope          // (y-b)/a = Expeted time for data
    ExTime = MinMax_Time[0] + ExTime
    ExTime = Math.floor(ExTime)
  }

  return {ExValue, ExTime}
}

/* Shared functions */
function isBatchClosed(Batch){
  return (Batch && Batch.state && Batch.state === DataStates.CLOSED)?true:false
}
function getBatchWeights(Batch){
  return (Batch && Batch.closed && Batch.closed.weighings)?Batch.closed.weighings:null
}
function GetBatchEnd(Batch, Curve) {
  if (Batch && Batch.time && Curve) {
    var currentTime = new Date(Batch.time) //Does not allow weight's after Batch end
    var CurveLenght = Object.keys(Curve.data)[Object.keys(Curve.data).length-1]

    for (var i = 0; i < CurveLenght; i++) {
      if (i < CurveLenght-1 && Curve.data[i+1] <= Batch.datas.target) {
        currentTime = new Date(AccumulateTime(currentTime, Curve.periode))
      }
    }
    return currentTime.getTime();
  }
  return null
}
function GetNewestWeight({Weighings, AfterTime, BeforeTime}) {
  var Weighing = null;
  if (Weighings) {
    for (const _weighing in Weighings) {
      var weighing = WeighingsStore[_weighing]
      if (weighing && !weighing.deleted && !weighing.edited && (!Weighing || Weighing.time < weighing.time)) {
        if ((!AfterTime || weighing.time >= AfterTime) &&
          (!BeforeTime || weighing.time <= BeforeTime)) {
          Weighing = weighing
        }
      }
    }
  }
  return Weighing;
}
function getCurveData({Graphs, Batch}) {
  if (Batch && Graphs && !Graphs.data) {
    if (isBatchClosed(Batch) && Batch.gcurve) return Batch.gcurve;
    else if (Batch.gcurve && Graphs && Graphs[Batch.gcurve]) return Graphs[Batch.gcurve]
  } 
  else if (Graphs && Graphs.data) return Graphs
  return null
}
