async function WeightCalc({ Weighings, StartDate, EndDate, Batch, Graphs}, _rend) {
  //var state = _rend.getState()
  var PenWeight = 0
  var PenGrowth = 0

  var LastWeight = undefined;
  var LastWeightDay = undefined;
  var FirstWeightDay = undefined;
  var today = false

  var isVirtual = false
  var Forcast = null
  
  var isClosed = false
  if (!Weighings && !isBatchClosed(Batch)) {
    Weighings = getBatchWeights(Batch)
  }

  if (Weighings) {
    var GrowthCount = 0
    Weighings = Object.fromEntries(Object.entries(Weighings).sort(([,a],[,b]) => a.time<b.time?1:-1))
    
    Object.entries(Weighings).forEach(([Id,Weighing]) => {
      if (!Weighing.deleted && !Weighing.edited && ((!StartDate || !EndDate) || (Weighing.time >= StartDate && Weighing.time <= EndDate))) {
        PenWeight = Weighing.weight
        if (Weighing.count) PenWeight = Weighing.weight / Weighing.count;
        if (LastWeight !== undefined) {
          var weight = PenWeight - LastWeight
          PenGrowth += weight
          GrowthCount++;
        }

        LastWeight = PenWeight
        LastWeightDay = Weighing.time
        if ( new Date(LastWeightDay).setHours(0,0,0,0) === new Date(Date.now()).setHours(0,0,0,0) ) today = true;
        if (!FirstWeightDay) FirstWeightDay = Weighing.time;
      }
    })
    if (GrowthCount > 0) {
      var days = (LastWeightDay - FirstWeightDay) / (1000*60*60*24)
      PenGrowth = PenGrowth/days
    }
  }

  if (!today && Batch) {
    var Predict = await CurvePredict({Weighings, Batch, Graphs})
    if (Predict && Predict.Now && Date.now() >= new Date(LastWeightDay).getDate()) {

    }
  }

  
  return { PenWeight, PenGrowth, Forcast, isVirtual }
}

