import handleError from '../utils/handleError'
import { auth, database, firebase } from './Initial'

import FirebaseSection from '../../Store/Firebase/Section'

async function fetch({FarmId, PenId, SectionId, limitStart = 0, limitTo = 9999, DataTime, MemSections}) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/weighings";
    var res
    if (PenId) {
      console.log("Weight fetch called, getting Pen")
      ref += "/" + PenId
      await database().ref(ref).orderByChild('time').limitToLast(limitTo).once('value', snapshot => {
        var weighing = {[snapshot.key]:{}}
        snapshot.forEach(c => {
          weighing[snapshot.key][c.key] = c.val()
        })
        res = weighing
      })
    } else {
      console.log("Weight fetch called, getting Sections")
      res = await database().ref(ref + "/update_time").once('value').then(async (_weighingTime) => {
        //TODO: Section fetch cast :: in mem check ??
        var Weighings_Time = _weighingTime.val()
        var res2 = await FirebaseSection.action({FarmId}).then(sections => {
          console.log(sections)
          var itemsProcessed = 0;
          var length = 0;
          var newWeights = {update_time:Date.now()}
          var Sections = sections?sections.data:MemSections
          Object.entries(Sections).forEach(([section,data]) => {
            if (data && data.pens) {
              length += Object.keys(data.pens).filter(e => e !== 'update_time').length
              Object.keys(data.pens).filter(e => e !== 'update_time').forEach(pen => {
                fetchWeights({FarmId:FarmId, PenId:pen, limitStart:0, limitTo:limitTo}).then((weights) => {
                  itemsProcessed++
                  newWeights = {...newWeights, ...weights.data}
                  if (itemsProcessed === length) {
                    newWeights.update_time = Weighings_Time
                    resolve({type:Actions.UPDATE, path:DataRefs.WEIGHING, data:newWeights})
                  }
                }).catch(error => {
                  console.log(error)
                  resolve({type:Actions.UPDATE, path:DataRefs.WEIGHING, data:newWeights})
                })
              })
            }
          })
          if (!Sections.length || Sections.length <= 1) {
            //return newWeights
          }
        }).catch(error => {
          console.log(error)
        })
        return res2
      }).catch(error => {
        console.log(error)
      })
    }
    if (res) return res
  }
  return handleError({ message: 'Fecth Weights Error' })
}

