import handleError from '../utils/handleError'
import { auth, database, firebase } from './Initial'

async function fetch({FarmId, PenId}) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/pens";
    if (PenId){
      ref += "/" + PenId
    }
    var res = await database().ref(ref).once('value').then((_pensData) => {
      if (PenId) {
        var penData = _pensData.val()
        if (!penData) penData = {}
        if (!penData.datas) penData.datas = {}

        return {[PenId]: penData};
      } else {
        return _pensData.val()
      }
    })
    return res
  }
  return handleError({ message: 'Fecth Pens Error' })
}

export {
  fetch, 
}