import React, { useState, useEffect, Component } from 'react'
import {
  View,
  ActivityIndicator,
  Text,
  TextInput,
  TouchableOpacity,
  SafeAreaView,
  ScrollView
} from 'react-native'
import { useDispatch, useSelector, connect } from 'react-redux'

import { useTheme } from '../Theme'
import PropTypes from 'prop-types'
import { navigationRef, isApp } from '../Navigators/Root'

import { AccumulateTime } from '../Services/Firebase/Utils'
import { ContentProp } from './index'

import { VictoryBar, VictoryLine, VictoryScatter, VictoryChart, VictoryGroup, VictoryTheme, VictoryVoronoiContainer, VictoryZoomContainer, createContainer, VictoryTooltip, VictoryAxis, VictoryLegend } from "victory-native";
const VictoryZoomVoronoiContainer = createContainer("zoom", "voronoi");

function getRandomNumber(low, high) {
  var r = Math.floor(Math.random() * (high - low + 1)) + low;
  return r;
}
function getRandomColor() {
  var red_range = [15, 230];
  var green_range = [15, 230];
  var blue_range = [15, 230];
  var a_range = [0.3, 1];

  var red = getRandomNumber(red_range[0], red_range[1]);
  var green = getRandomNumber(green_range[0], green_range[1]);
  var blue = getRandomNumber(blue_range[0], blue_range[1]);

  var alpha = getRandomNumber(a_range[0] * 100, a_range[1] * 100) / 100;

  return {
    r: red,
    g: green,
    b: blue,
    a: alpha,
    rgbaValue: 'rgba(' + red + ',' + green + ',' + blue + ',' + alpha + ')'
  }
}

function CalcWeighningsSeries({WeighningsDatas, Series}) {
  var DataSets = []
  var Legends = []
  if (WeighningsDatas && WeighningsDatas.Batch) {
    var StartDate = new Date(WeighningsDatas.Batch.time)
    var EndDate = new Date(StartDate.setDate(StartDate.getDate()+(7*8)))
    
    if (WeighningsDatas.TempCurve) {
      var TempCurve = WeighningsDatas.TempCurve

      var TemplateData = []
      var currentTime = StartDate
      for (var i = 0; i < TempCurve.data.length; i++) {
        if (TempCurve.data[i]) {
          TemplateData.push({
            x:currentTime.getTime(), 
            y:TempCurve.data[i]
          })
        }
        if (i < TempCurve.data.length -1) {
          currentTime = new Date(AccumulateTime(currentTime, TempCurve.periode))
        }
      }
      EndDate = currentTime.getTime();
      TemplateData.push({
        x:EndDate, 
        y:TempCurve.data[TempCurve.data.length-1]
      })

      var deactive = false
      if (Series) {
        var index = Series.findIndex(i => i.name === WeighningsDatas.Batch.gcurve)
        if (index !== -1 && Series[index].deactivate) deactive = true
      }

      DataSets.push({
        type: "Line",
        name: WeighningsDatas.Batch.gcurve,
        color: "#000",
        data: TemplateData,
        deactive: deactive
      })
      Legends.push({name: WeighningsDatas.Batch.gcurve, symbol: { fill: "#000"}})
    }
  }
  return {DataSets, Legends}
}

class CurveViewer extends Component {
  constructor(props) {
    super(props);

    const {Legends, DataSets, StartDate, EndDate, Domain } = this.setData(props)
    this.state = {
      DataSets: DataSets,
      Legends: Legends,

      StartDate: StartDate,
      EndDate: EndDate,

      Labels: [],

      entireDomain: Domain,
      zoomedDomain: Domain
    }
  }

  setData(Props, State) {
    var D = new Date()
    var StartDate = D.getTime()
    var EndDate = new Date(D.setDate(D.getDate()+(7*8)))
    var Legends = []     //Name lables
    var DataSets = []    //Data Points
    var Domain = [];

    if (Props.WeighningsDatas) {
      var WeighningSeries = CalcWeighningsSeries({WeighningsDatas:Props.WeighningsDatas, Series:State?State.DataSets:null})
      DataSets = DataSets.concat(WeighningSeries.DataSets)
      Legends = Legends.concat(WeighningSeries.Legends)
    }
    var Domain = this.getEntireDomain(DataSets)
    return {Legends, DataSets, StartDate, EndDate, Domain }
  }

  getData() {
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps) {
      const {Legends, DataSets, StartDate, EndDate, Domain } = this.setData(this.props, this.state)
      
      this.setState({
        Legends: Legends,
        DataSets: DataSets,
  
        StartDate: StartDate,
        EndDate: EndDate,

        entireDomain: Domain,
        zoomedDomain: Domain
      }, () => {
        this.forceUpdate()
      });
    }
  }

  componentDidMount() {
    this.getData();
  }

  componentWillUnmount(){
  }

  getEntireDomain(series) {
    var MinMax = {y:[0,1], x: [0,1]}
    series.forEach(element => {
      if (!element.deactivate && element.data) {
        var data = element.data
        if (data.length > 0) {
          var min = _.minBy(data, d => d.y).y
          var max = _.maxBy(data, d => d.y).y
          if (min < MinMax.y[0] || MinMax.y[0] === 0) MinMax.y[0] = min
          if (max > MinMax.y[1]) MinMax.y[1] = max
          min = _.minBy(data, d => d.x).x
          max = _.maxBy(data, d => d.x).x
          if (min < MinMax.x[0] || MinMax.x[0] === 0) MinMax.x[0] = min
          if (max > MinMax.x[1]) MinMax.x[1] = max
        }
      }
    })
    return MinMax
  }

  getZoomDomain(Props){
    const Serie = this.state.DataSets;
    const filteredSeries = [];
    Serie.forEach(element => {
      var filtered = []
      if (element.type === "Line") {
        element.data.forEach(data => {
          if (!data.predict || Props.Predict) {
            filtered.push(data)
          }
        })
      }
      if (filtered.length > 0) {
        filteredSeries.push({...element, data: filtered})
      }
    });
    return filteredSeries;
  }

  onDomainChange(domain) {
  	this.setState({
    	zoomedDomain: domain,
    });
  }

  render() {
    var App = isApp()

    var Legends = [this.state.zoomedDomain.x[0]]
    while(Legends[Legends.length-1] < this.state.zoomedDomain.x[1]){
      if (this.props.Display) Legends.push(this.state.DataSets[0].data[Legends.length-1].x )
      else Legends.push(Legends[Legends.length-1] + 3600000)
    }

    if (this.props.Display && this.state.DataSets) {
      this.state.DataSets.forEach((e,i) => {
        Legends.push(i)
      })
    }
    var LastLegend = ""
    return(
      <View style={[{height: 650, width: '100%', alignItems:'center'}]}>
        {(Legends && Legends.length > 0) && (this.state.DataSets && this.state.DataSets.length > 0)&&(
          <View style={[{height: 600, width: '100%'}, {maxWidth:isApp()?450:950}, {borderWidth:2, borderRadius:10, minHeight:400, marginVertical:5, marginHorizontal:5, padding:2.5}]}>
            <VictoryChart
              scale={{ x: "time" }}
              width={isApp()?370:950}
              height={600}
              padding={50}
              domain={this.state.entireDomain}
              theme={VictoryTheme.material}
              containerComponent={
                <VictoryZoomVoronoiContainer
                  voronoiDimension="x"
                  zoomDimension="x"
                  mouseFollowTooltips
                  onZoomDomainChange={this.onDomainChange.bind(this)}
                  labels={({ datum }) => {
                    if (datum.childName === "none" || !datum.y) return;
                    var DateTime = new Date(datum.x)
                    var format = DateTime.getDate() + "/" + (DateTime.getMonth()+1) + "/" + DateTime.getFullYear() + " " + + ("0" + DateTime.getHours()).slice(-2) + ":" + ("0" + DateTime.getMinutes()).slice(-2)
                    return datum.childName + "\n" + format + " : " + datum.y.toFixed(2) + "\n "
                  }}
                  labelComponent={<VictoryTooltip cornerRadius={0} flyoutStyle={{fill: "white"}}/>}
                />
              }
            >
              <VictoryAxis crossAxis={false}
                scale={{ x: "time" }}
                tickCount={8}
                minDomain={{ x: 1 , y: 0}}
                tickFormat={(t) => {
                  var DateTime = new Date(t)
                  var format = DateTime.getDate() + "/" + (DateTime.getMonth()+1) + "/" + DateTime.getFullYear() + " " + ("0" + DateTime.getHours()).slice(-2) + ":" + ("0" + DateTime.getMinutes()).slice(-2)
                  if (LastLegend === format) return;
                  else LastLegend = format
                  return format
                }}
                theme={VictoryTheme.material}
                standalone={false}
                fixLabelOverlap
                tickValues={Legends}
                style={{
                  grid: { stroke: "#818e99", strokeWidth: 0.5 },
                }}
              />
              <VictoryAxis dependentAxis crossAxis={false}
                tickCount={20}
                minDomain={{ x: 0 , y: 0}}
                tickFormat={(t) => {
                  return Math.round(t * 10) / 10
                }}
                theme={VictoryTheme.material}
                standalone={false}
                style={{
                  grid: { stroke: "#818e99", strokeWidth: 0.5 },
                }}
              />
              {/* DATA HANDLERS */}
              {/* Growth Curves "Control Weight's" */
                this.state.DataSets && this.state.DataSets.map((Serie, i) => {
                  console.log(Serie)
                  if (Serie.type === "Line" && !Serie.deactivate) {
                    return (
                      <VictoryGroup key={"Line_" + Serie.name}
                        data={ Serie.data }
                      >
                        <VictoryLine
                          style={{
                            data: { stroke: Serie.color, strokeWidth: ({ active }) => active ? 4 : 2},
                          }}
                          name={ Serie.name }
                        />
                        <VictoryScatter
                          size={2}
                          style={{data: { stroke: "#000", strokeWidth: 0.5, fill: Serie.color},}}
                          name={"none"}
                        />
                      </VictoryGroup>
                    );
                  }
                })}
            </VictoryChart>
          </View>
        )}
      </View>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(CurveViewer);